<script>

  import {Vue} from 'vue';
  import {bus} from './../config_wireframe.js';
  import uiListMixinLib from './../components/mixins/lib/ui-list.js';
  import userMixinLib from './../components/mixins/lib/user_mixin_lib.js';

  import {
    RSocketClient,
    JsonSerializer,
    IdentitySerializer,
    JsonSerializers,
    BufferEncoders,
    encodeAndAddCustomMetadata,
    encodeAndAddWellKnownMetadata,
    TEXT_PLAIN,
    MESSAGE_RSOCKET_COMPOSITE_METADATA,
    MESSAGE_RSOCKET_ROUTING,
  } from 'rsocket-core';
  import RSocketWebSocketClient from "rsocket-websocket-client";
  import {Flowable, Single} from 'rsocket-flowable';

  export default {
    mixins : [uiListMixinLib, userMixinLib],
    data: function() {
      return {
          api : "tasks",
          isMobileDevice: true,
          resizeTimeout: null,
          appName: this.$route.params.app_name,
          searchQuery: '',
          wsSearchQuery: '',
          ongoingConf: {},
          conference_list: [],
          my_conference_list: [],
          starred_conferences_list: [],
          conf_service_error: '',
          workStatus: '',
          workStatusList: [],
          ws_list_error: '',

          my_whiteboard_docs_list: [],
          wbdocSearchQuery: '',
          isCallActive: false, // If the user is on an active call, it must not show "Join" options,

          shouldCollapseMiddleMessages: false,
          collapseText: 'Show',
          list: [],

          isTaskChosen: false,
          selectedTask: {"id":"123"},
          displayNewForm : false,
          resetTime : {
              time : new Date().getTime()
          },
          isCategoryFilterOn : false,
          filterByCategoryId : null,

          activityList : [],
          currentPageNumberOfActivities : 1,
          isActivityListIntersectionObserverFired : false,
          allActivityRecordsFetched : false,


          rsocket : {
            socket : null,
            streamSubscription : null,
            notificationSubscription : null
          }
      }
    },
    methods: {

      connectToNotificationSocket()
      {
          console.log("Logged in user ", this.loggedInUser);

          let socket = this.rsocket.socket;
          let route = "connect-to-realtime-notifications";

          let requestResponseData = {
              metadata : String.fromCharCode(route.length) + route,
              data : {"userId" : this.loggedInUser.userId}
          };

          let handleRSocketResponse = (response) => {
            console.log("RSocket's notification socket sent complete response : ", response);
          };

          socket.requestResponse(requestResponseData)
                .subscribe({
                  onSubscribe: sub => {
                      this.rsocket.notificationSubscription = sub;
                      console.log("Notification subscription successful ", sub);
                  },
                  onComplete : response => handleRSocketResponse(response),   // This will be called when the data upstream is all sent.
                  onError    : error    => console.error(error),              //
                });

      },
      connectToRSocket(){

        console.log("connecting with Rocket @9000...");

            const client = new RSocketClient({
                transport : new RSocketWebSocketClient({
                    url: "ws://localhost:9000/rsocket"
                }),
                serializers: {            // send/receive JSON objects instead of strings/buffers
                    data: JsonSerializer,
                    metadata: IdentitySerializer
                },
                setup: {
                    dataMimeType: 'application/json',
                    keepAlive: 3600000,     // 3600000 = 8hrs  // When this time elapses without any data, then server throws this exception :: io.rsocket.exceptions.ConnectionErrorException: No keep-alive acks for 100000 ms
                    lifetime: 3600000,
                    metadataMimeType: 'message/x.rsocket.routing.v0',
                },
                responder : {
                  fireAndForget : (payload) => {

                      console.log("Message pushed from server, ", payload);
                      // if(payload.metadata.get(Metadata.ROUTE) == "send.to.location") {
                      //     const radar = payload.data;
                      //     this.map.panTo([radar.location.lat, radar.location.lng]);
                      // }


                      if(payload.metadata == "new-notification-update")
                      {
                            let notification = payload.data;
                            console.log("New Notification :", notification);

                            if(window.Notification && Notification.permission !== "denied") {


                              	Notification.requestPermission((status)=> {  // status is "granted", if accepted by user
                              		var n = new Notification(notification.title, {
                              			body: notification.description,
                              			icon: '/resources/images/taskone-logo.png', // optional,
                                    requireInteraction : true,
                                    renotify :true,
                                    tag : notification.id
                              		});
                              	});


                            }
                      }
                      else if(payload.metadata == "category-activity-update"){

                          if(payload.data){

                            let item = payload.data;

                            item["taskSno"] = item["taskInfo"].split("~")[0];
                            item["taskName"] = item["taskInfo"].split("~")[1];
                            item["taskCreatedBy"] = item["taskInfo"].split("~")[2].split("#")[1];

                            if(item.createdOn !== null)
                            {
                                item.createdOn = new Date(item.createdOn).toLocaleString()
                                                                         .slice(0, 17)
                                                                         .replace(',', '')
                                                                         .replace('T',' ');
                            }


                            if(item.propertyChanges !== null)
                            {
                                item.propertyChanges.forEach(propChange => {

                                    if(propChange.toDate !== null)
                                    {
                                        propChange.toDate = new Date(propChange.toDate).toLocaleString()
                                                                                       .slice(0, 17)
                                                                                       .replace(',', '')
                                                                                       .replace('T',' ');
                                    }

                                    // Since the status will contain the hash splitter, we need to extract only the label.
                                    if(propChange.label === "status")
                                    {
                                        propChange.to = propChange.to.split("#")[1];
                                    }

                                });
                            }

                            item.hide = true;
                            this.activityList.unshift(item);

                            setTimeout(()=>{

                                // item.hide  = false;
                                // Vue.set(this.activityList,0,item);
                                // this.$set(this.activityList, 0, item);
                                //
                                // setTimeout(()=>{
                                //   document.querySelectorAll(".new_activity").forEach((el)=>{
                                //     el.classList.remove("new_activity");
                                //   });
                                //
                                console.log("Removing new_activity from " + item["id"]);
                                document.getElementById("channel_task_activity_content_" + item["id"]).classList.remove("new_activity");
                                document.getElementById("channel_task_activity_content_" + item["id"]).classList.add("pulse_activity");
                                // },2000);
                            },1000);

                            document.getElementById("activity_channel_counter").style.transform="rotateX(360deg)";
                            setTimeout(()=>{
                                document.getElementById("activity_channel_counter").style.transform=null;
                            },2000);
                          }

                      }
                  }
                }
            });

            client.connect().subscribe({
                onComplete: socket => {
                  this.rsocket.socket = socket;
                  console.log("RSocket connected.");

                },
                onError: error => {
                  console.log("got connection error");
                  console.error(error);
                },
                onSubscribe: cancel => {
                  // call cancel() to abort
                  console.log("client.connect().subscribe's onSubscribe : ", cancel);
                }
            });


      },
      sendStreamConnectOverRSocket(categoryId){

          // Cancel the existing streamSubscription before making a new one.
          this.cancelStreamSubscription();

          let socket = this.rsocket.socket;
          let route = "connect-to-stream";

          let requestResponseData = {
               metadata : String.fromCharCode(route.length) + route,
               data : {"categoryId" : categoryId, "origin" : "Client", "interaction" : "Request"},
          };

          let handleRSocketResponse = (response) => {
            console.log("RSocket's stream sent complete response (No more available data.): ", response);
          };

          console.log("Requesting to connect to stream to listen for activities from categoryId " + categoryId + "..");

          let subscription = null;

          socket.requestStream(requestResponseData)
                .subscribe({
                  onSubscribe: sub => {
                      this.rsocket.streamSubscription = sub;
                      console.log(`Client is establishing a channel`);
                      //subscription.request(0x7fffffff);
                      this.rsocket.streamSubscription.request(1);
                  },
                  onComplete : response => handleRSocketResponse(response),   // This will be called when the data upstream is all sent.
                  onError    : error    => console.error(error),              //
              		onNext: value => {                                          // To process the individual item of the upstream as they are sent.
              		  console.log("got next value in requestStream..", value);
              		  //this.received.push(value.data);
              		},
                });
          console.log("Requesting to connect to stream done and waiting for its response..");
      },
      cancelStreamSubscription(){
        if(this.rsocket!==null && this.rsocket!==undefined && this.rsocket.streamSubscription!==null)
            this.rsocket.streamSubscription.cancel(); //There won't be any callback for this operation.
      },

      newItem(){

        this.isTaskChosen = false;
        this.displayNewForm = true;
        this.selectedTask = {};

        // This prop is passed to the new-form component, so that the watcher in it resets the form whenever this value changes.
        // Reason : Since the new-form component is alive, any data entered into it stays on the form until its reset.
        // Since we cannot pass events from parent to child to reset the data of the newFormObject={}, without using event-bus,
        // we are just changing the value of a prop, that can be watched everytime when the "New" button is clicked. The watcher resets the newFormObject={}
        // whenever there is a change in this prop's value.
        this.resetTime = {
            time : new Date().getTime()
        }

      },
      showTaskDetails(taskId, idx) {
          // Toggle the active state of the list-item
          document.querySelectorAll('.active').forEach(item => item.classList.remove('active'))
          document.querySelector('#' + 'task_' + taskId).classList.add('active')

          this.selectedTask = this.list[idx]
          this.isTaskChosen = true

          this.$router.push({
              name: "view-task-inbox-ms",
              params: {
                  "taskId" : this.selectedTask.id,
                  "subtaskId" : "none"
              }
          });

          //alert("Router pushed to " + this.selectedTask.id);
      },
      collapseMiddleMessages() {
        console.log('shouldCollapseMiddleMessages = ' + this.shouldCollapseMiddleMessages)
        this.shouldCollapseMiddleMessages = !this.shouldCollapseMiddleMessages

        this.collapseText = this.shouldCollapseMiddleMessages ? 'Collapse' : 'Show'
      },
      completeTask() {},

      goTo(routeName) {
        this.$router.push({
          name: routeName,
          params: {}
        })
      },
      goToHomePage() {
        this.$router.push({
          name: 'home',
          params: {}
        })
      },
      updateFetchURL(){
          let searchQuery = (this.searchQuery!=="")? this.searchQuery: "all";

          if(this.isCategoryFilterOn)
              this.url = "./tasks/list-by-category/" + this.filterByCategoryId + "/" + this.pageNumber + "/" + searchQuery;
          else
              this.url = "./" + this.api + "/list/" + this.pageNumber + "/" + searchQuery;
      },
      handleData(dataResponse){

          if (dataResponse.data["actionResult"] === 1) {

              let listOfTasks = (dataResponse.data["bean"]);//.slice(0, 10);

              // Since we are pulling 20 records per page, then if the resulting size is lesser than 20, then it means
              // no more records are available.
              if(listOfTasks.length <20)
              {
                this.allRecordsFetched = true;
              }

              listOfTasks.forEach(task => {

                  if(task.createdOn !== null)
                  {
                      task.createdOn = new Date(task.createdOn).toLocaleString()
                                                               .slice(0, 17)
                                                               .replace(',', '')
                                                               .replace('T',' ');
                  }

                  if(task.dueDateTime !== null)
                  {
                      task.dueDateTime = new Date(task.dueDateTime).toLocaleString()
                                                               .slice(0, 17)
                                                               .replace(',', '')
                                                               .replace('T',' ');
                  }

                  if(task.isOnline!==null && task.isOnline===1)
                  {
                      task.isOnline = "LIVE";
                  }

                  if(task["createdBy"]!==null)
                    task["createdBy"] = task["createdBy"].split("#")[1];


                  let assigneeListString = "";
                  task.assignees.forEach((assignee) => {
                      assigneeListString = assignee["firstName"] + " " + assignee["lastName"] + "<br>" + assigneeListString;
                  });
                  task["assigneeListString"] = assigneeListString;

                  //To prevent null error in vue-render if the "assigneeIds" prop is null
                  if(task["assigneeIds"]===null)
                    task["assigneeIds"] = [];

                  //To prevent null error in vue-render if the "assigneeIds" prop is null
                  if(task["followeIds"]===null)
                    task["followeIds"] = [];

                  //To prevent null error in vue-render if the "subTaskIds" prop is null
                  if(task["subTaskIds"]===null)
                    task["subTaskIds"] = [];

                  this.list.push(task);
              });


              // When the page is refreshed with a valid taskId, then it must display the taskView.
              // Here only the view is displayed.
              // The actual taskInformation will be pulled from the task-view-ms.vue in its mounted() lifecycle hook.
              if(this.$route.params.taskId!==null && this.$route.params.taskId!==undefined && this.$route.params.taskId!=="all")
              {
                  this.isTaskChosen = true;

                  setTimeout(()=>{

                    let taskId = this.$route.params.taskId;

                    // Toggle the active state of the list-item
                    document.querySelectorAll('.active').forEach(item => item.classList.remove('active'));
                    document.querySelector('#' + 'task_' + taskId).classList.add('active');

                    //If the chosen task is not in the visible area, then scroll the list to bring it to the view.
                    let target = document.querySelector('#' + 'task_' + taskId);
                    target.parentNode.scrollTop = target.offsetTop - target.parentNode.offsetTop;
                  }, 250);
              }
          }
          else {
              console.error("Error", dataResponse);
              //alert("Error : " + dataResponse)
          }
      },
      refreshPaneList(){
        console.log("Refresh pane list");
        this.resetRefreshList();
      },
      fetchCategoryActivities(){
          //this.filterByCategoryId

          // If all records are fetched, make no calls to the server again.
          if(this.allActivityRecordsFetched)
            return false;

          let url = "./task-activity/list-by-category/" + this.filterByCategoryId + "/"  + this.currentPageNumberOfActivities;
          try
          {
              // VueJS ajax call-1
              axios.get(url)
                  .then((dataResponse) => {
                        console.log("Activity List: " , dataResponse);
                        let list = dataResponse.data.bean;

                        // Since we are pulling 20 records per page, then if the resulting size is lesser than 20, then it means
                        // no more records are available.
                        if(list.length <20)
                        {
                          this.allActivityRecordsFetched = true;
                        }

                        list.forEach((item) => {

                            item["taskSno"] = item["taskInfo"].split("~")[0];
                            item["taskName"] = item["taskInfo"].split("~")[1];
                            item["taskCreatedBy"] = item["taskInfo"].split("~")[2].split("#")[1];

                            if(item.createdOn !== null)
                            {
                                item.createdOn = new Date(item.createdOn).toLocaleString()
                                                                         .slice(0, 17)
                                                                         .replace(',', '')
                                                                         .replace('T',' ');
                            }


                            if(item.propertyChanges !== null)
                            {
                                item.propertyChanges.forEach(propChange => {

                                    if(propChange.toDate !== null)
                                    {
                                        propChange.toDate = new Date(propChange.toDate).toLocaleString()
                                                                                       .slice(0, 17)
                                                                                       .replace(',', '')
                                                                                       .replace('T',' ');
                                    }

                                    // Since the status will contain the hash splitter, we need to extract only the label.
                                    if(propChange.label === "status")
                                    {
                                        propChange.to = propChange.to.split("#")[1];
                                    }

                                });
                            }

                            item.hide = false;
                            this.activityList.push(item);
                        });

                        //If there are more records to fetch, then start observing the loadMore-intObsrv-trigger.
                        if(!this.isActivityListIntersectionObserverFired)
                        {
                          this.initLoadMoreIntersectionObserverInActivityList();
                          this.isActivityListIntersectionObserverFired = true;
                        }
                  })
                  .catch((error) => {
                      console.error("fetchCategoryActivities Error : ", error);
                  });
          }
          catch(error)
          {
              console.error("Unpaginated List data fetch error : ", error);
          }
      },
      initLoadMoreIntersectionObserverInActivityList(){

          //console.log("bus = ", bus);

          this.observer = new IntersectionObserver((entries) => {
              if (entries[0].intersectionRatio <= 0) { // If not in view
                  console.log("Load more is not in view. " + "#" + this.observerElement);
              }
              else{
                  console.log("Load more is in view. " + "#" + this.observerElement);
                  //observer.unobserve(document.getElementById(observerElement));

                  //Increment the current page number
                  this.currentPageNumberOfActivities += 1;
                  this.fetchCategoryActivities();
              }
          },
          {
            root: null,
            rootMargin: "0px",
          });

          setTimeout(()=>{
              this.observer.observe(document.getElementById("isActivityListIntersectionObserver"));
          }, 1000);

      },
    },
    created: function() {},
    computed: {
      storeCounter: function() {
        //console.log('Accessing vuex store ', this.$store)
        //console.log('Store data accessed from home page : ' + this.$store.getters.count)
      },
      isConferenceCallActive: function() {
        return this.$store.getters.isConferenceCallActive
      }
    },
    mounted: function() {
      //this.$store.commit('increment');
      //this.storeCounter();
      //console.log('Accessing vuex store ', this.$store)
      //console.log('Store data accessed from home page : ' + this.$store.getters.count)
      //console.log('Accessing computed property : ' + this.isConferenceCallActive)
      //console.log('Done.')

      this.getList();
      this.connectToRSocket();

      bus.on("newTaskEvent", (data)=>{
          console.log("Event received in bus.", new Date());
          this.newItem();
      });

      bus.on("filterByCategory" , (data)=>{

          if(data=="all")
          {
            this.isCategoryFilterOn = false;
            this.filterByCategoryId = null;
            this.resetRefreshList();

            //Reset ActivityList's data and intersection-observers
            this.activityList = [];
            this.currentPageNumberOfActivities = 1;
            this.isActivityListIntersectionObserverFired = false;
            this.allActivityRecordsFetched = false;
          }
          else
          {
            this.isCategoryFilterOn = true;
            this.filterByCategoryId = data;
            this.resetRefreshList();

            //Reset ActivityList's data and intersection-observers
            this.activityList = [];
            this.currentPageNumberOfActivities = 1;
            this.isActivityListIntersectionObserverFired = false;
            this.allActivityRecordsFetched = false;
            this.fetchCategoryActivities();

            //When the specific category is clicked, then connect to the rsocket stream and supply the categoryID.
            this.sendStreamConnectOverRSocket(this.filterByCategoryId);
          }
      });





    },
    watch: {

    }
  }

</script>


